<template>
  <el-main>
    <h3 align="left"></h3>
       <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/User/Part/Putjob'}"></el-breadcrumb-item>
  <el-breadcrumb-item :to="{ path: '/User/Part/Putjob/TodoTask'}"></el-breadcrumb-item>
  <el-breadcrumb-item :to="{ path: '/User/Part/Putjob/DoneTask'}"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
</el-breadcrumb>
    <el-table :data="tableData" v-loading="loading">
      <el-table-column prop="ID" label="" sortable  width="100"></el-table-column>
      <el-table-column prop="type" label="" >
         <template slot-scope="scope">
         <span style="color:#00b38a"  v-if="scope.row.type==2"></span>
          <span style="color:orange"  v-if="scope.row.type==3"></span>
          <span style="color:green"  v-if="scope.row.type==4"></span>
          <span style="color:red"  v-if="scope.row.type==5"></span>
          <span   v-if="scope.row.type==6"></span>
         </template>
      </el-table-column>
      <el-table-column prop="price" sortable label="" width="100"></el-table-column>
      <el-table-column prop="number"  label="" width="150"></el-table-column>
      <el-table-column prop="state"  label="">
        <template slot-scope="scope">
         <span style="color:orange"  v-if="scope.row.state==0"></span>
          <span style="color:#00b38a"  v-if="scope.row.state==1"></span>
         </template>
      </el-table-column>
      <el-table-column prop="state_s"  label="">
         <template slot-scope="scope">
         <span style="color:orange"  v-if="scope.row.state_s==0"></span>
          <span style="color:#00b38a"  v-if="scope.row.state_s==1"></span>
           <span style="color:red"  v-if="scope.row.state_s==2"></span>
         </template>
      </el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button size="mini"  @click="Pass(scope.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button size="mini" @click="Reject(scope.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button size="mini" @click="Complain(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-main>
</template>

<script>
export default {
  name: "Task",
  data() {
    return {
      tableData: [],
      loading:false
    };
  },
  methods: {

    //
    getTask: function(vm) {
      if (this.user_id === "") return;
      vm.loading=true;
      var URL = "http://localhost:8082/module/user/provider_task_done";
      var jsonData = { offset: 0, number: 100 };
      var axios = {
        method: "get",
        url: URL,
        widthCredentials: false,
        params: jsonData
      };
      vm.$http(axios)
        .then(function(res) {
          if (res.status == 200) {
            if (res.data.code == 200) {
              var number = res.data.number;
              var content = res.data.content;
              var jsonContent = content;
              for (var i = 0; i < number; i++) {
                var temp = jsonContent[i];
                var tempIndex = {
                  ID: 0,
                  type: 0,
                  price:"",
                  number:"",
                  state:0,
                  state_s:0
                };
                tempIndex.ID = temp.tid;
                tempIndex.type = temp.type;
                tempIndex.price = "$" + String(temp.reward);
                tempIndex.number = temp.sid;
                tempIndex.state=temp.accept_status;
                tempIndex.state_s=temp.verify;
                
                vm.tableData.push(tempIndex);
              }
              
            } else {
              alert(res.data.msg);
            }
          } else alert("");
          vm.loading=false;
        })
        .catch(function(err) {
          vm.loading=false;
          console.log(err);
        });
    },

    Pass:function(row){
      var jsonData = {
        tid: parseInt(row.ID),
        sid:row.number,
        verify:1
      };
      var axios = {
        method: "put",
        url: "http://localhost:8082/module/user/task_verify",
        widthCredentials: false,
        data: jsonData
      };
      this.$http(axios).then(function(res){
          if(res.status==200) {
           alert("");
          }
          else alert("");
        }).catch(function(err){
          console.log(err);
          alert("");
        });
    },

    Reject:function(row){
      var jsonData = {
        tid: parseInt(row.ID),
        sid:row.number,
        verify:2
      };
      var axios = {
        method: "put",
        url: "http://localhost:8082/module/user/task_verify",
        widthCredentials: false,
        data: jsonData
      };
     
      this.$http(axios).then(function(res){
        if(res.status==200) {
          alert("");
        }
        else alert("");}).catch(function(err){
          console.log(err);
          alert("");
      });
    },

    Complain:function(row){
      this.$router.push({
        name: 'Complain',
        params: {
          id: row.ID,
          publisher: row.number
        }
      })
      const h = this.$createElement;
      this.$notify({
        title: '',
        message: h('i', { style: 'color: teal'}, 
        ''),
      });
    }
  },
  created() {
    this.getTask(this);
  }
};
</script>

<style scoped>
</style>


